import { Component, OnInit, Input, ViewEncapsulation } from "@angular/core";
import { HardSkillModule } from "src/app/modules/hardskills/hard-skill/hard-skill.module";

@Component({
  selector: "app-skill",
  templateUrl: "./skill.component.pug",
  styleUrls: ["./skill.component.scss"]
})
export class SkillComponent implements OnInit {
  @Input() skill: HardSkillModule;
  @Input() filterConfiguration;
  @Input() name: String;

  constructor() {}

  getProgressClasses() {
    return this.skill.ease >= 75
      ? "bg-maincolor3"
      : this.skill.ease >= 60
      ? "bg-maincolor2"
      : this.skill.ease >= 50
      ? "bg-maincolor5"
      : "bg-maincolor";
  }

  ngOnInit() {}

  displayIfEaseFiltered() {
    return Object.values(this.filterConfiguration).indexOf("002") != -1
      ? "1"
      : "0";
  }

  displayIfAchievementFiltered() {
    return Object.values(this.filterConfiguration).indexOf("003") != -1
      ? "block"
      : "none";
  }
}
