import { Component, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { ContactService } from "src/app/services/contact/contact.service";
import { SendMessageModule } from "src/app/modules/contact/send-message/send-message.module";

@Component({
  selector: "app-contact",
  templateUrl: "./contact.component.pug",
  styleUrls: ["./contact.component.scss"]
})
export class ContactComponent implements OnInit {
  private items;
  private mailForm;

  constructor(
    private formBuilder: FormBuilder,
    private contactService: ContactService
  ) {
    this.mailForm = this.formBuilder.group({
      name: "",
      address: "",
      message: "",
      email: "",
      phone: "",
      subject: ""
    });
  }

  ngOnInit() {}

  sendMessage(e: SendMessageModule) {
    e.event.preventDefault();
    //console.log(e);
    this.contactService.sendMessage(e.inputs).subscribe(ins => {
      console.log(ins);
    });
  }
}
