import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { DomSanitizer, SafeHtml } from "@angular/platform-browser";
import { CardModule } from "src/app/modules/cards/cards/card.module";

@Component({
  selector: "app-card",
  templateUrl: "./card.component.pug",
  styleUrls: ["./card.component.scss"]
})
export class CardComponent implements OnInit {
  @Input() id: number;
  @Input() level: number;
  @Input() html: SafeHtml;
  @Input() inlineHtml: string;
  @Input() htmlLines: string[];
  @Input() number: number;
  @Input() selected: number;
  @Input() cardsCount: number;

  @Output() incrementCards: EventEmitter<number> = new EventEmitter();
  @Output() decrementCards: EventEmitter<number> = new EventEmitter();

  constructor(private sanitizer: DomSanitizer) {}

  ngOnInit() {
    this.html = this.sanitizer.bypassSecurityTrustHtml(
      !this.inlineHtml ? this.htmlLines.join("") : this.inlineHtml
    );
  }

  emitIncrement() {
    this.incrementCards.emit(this.number);
  }
  emitDecrement() {
    this.decrementCards.emit(this.number);
  }
}
