import { Component, OnInit } from "@angular/core";
import { CardsService } from "src/app/services/cards/cards.service";
import { CardModule } from "src/app/modules/cards/cards/card.module";

@Component({
  selector: "app-cards",
  templateUrl: "./cards.component.pug",
  styleUrls: ["./cards.component.scss"]
})
export class CardsComponent implements OnInit {
  private cards: CardModule[];
  private selectedCard: number = 0;

  constructor(private cardsService: CardsService) {
    this.cardsService.getCards().subscribe(cards => {
      this.cards = cards;
      this.updateLevel();
    });
  }

  incrementCards(number: number) {
    this.selectedCard = this.getWarpedIndex(this.selectedCard + 1);
    this.updateLevel();
  }
  decrementCards(number: number) {
    this.selectedCard = this.getWarpedIndex(this.selectedCard - 1);
    this.updateLevel();
  }

  updateLevel() {
    const n = this.cards.length;
    const n2 = Math.ceil(n / 2);

    let step = this.selectedCard;

    for (let i = 1, t = n2; i < t; ++i) {
      step = this.getWarpedIndex(step - 1);
      this.cards[step]["level"] = i;
    }

    step = this.selectedCard;
    this.cards[step]["level"] = 0;

    for (let i = 1, t = n2; i < t; i++) {
      step = this.getWarpedIndex(step + 1);
      this.cards[step]["level"] = i;
    }
  }

  getWarpedIndex(value: number) {
    return (this.cards.length + value) % this.cards.length;
  }

  getLevelClass(card: any) {
    return "lvl-" + card.level;
  }

  ngOnInit() {}
}
