import { Component, OnInit, Output, EventEmitter, Input } from "@angular/core";
import { FilterModule } from "src/app/modules/types/filter/filter.module";
import { FilterService } from "src/app/services/types/filter.service";
import { FilteredModule } from "src/app/modules/types/filtered/filtered.module";

@Component({
  selector: "app-multifilter",
  templateUrl: "./multifilter.component.pug",
  styleUrls: ["./multifilter.component.scss"]
})
export class MultifilterComponent implements OnInit {
  @Input() filterSet: FilterModule[][];
  @Input() filterConfiguration;
  @Input() name: String;

  @Output() filterSkills: EventEmitter<FilterModule> = new EventEmitter();
  @Output() addFilters: EventEmitter<FilterModule> = new EventEmitter();
  @Output() removeFilter: EventEmitter<FilteredModule> = new EventEmitter();

  constructor() {}

  getRemaining() {
    return this.filterSet && this.filterSet.length == 0 && this.filterSet[0]
      ? 1
      : this.filterSet[0].length - 1 - this.filterSet.length;
  }

  emitAddFilters(e) {
    this.addFilters.emit(e);
  }

  emitChange(e) {
    this.filterSkills.emit(e);
  }

  emitRemoveFilter(e) {
    this.removeFilter.emit(e);
  }

  ngOnInit() {}
}
