import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";

@NgModule({
  declarations: [],
  imports: [CommonModule]
})
export class FilteredModule {
  event: Object;
  index: number;
  code: String;

  constructor(event: Object, index: number, code: String) {
    this.code = code;
    this.event = event;
    this.index = index;
  }
}
