import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { FilterModule } from "src/app/modules/types/filter/filter.module";
import { FilteredModule } from "src/app/modules/types/filtered/filtered.module";
import { RemoveFilterModule } from "src/app/modules/types/remove-filter/remove-filter.module";

@Component({
  selector: "app-skill-filter",
  templateUrl: "./skill-filter.component.pug",
  styleUrls: ["./skill-filter.component.scss"]
})
export class SkillFilterComponent implements OnInit {
  @Input() filters: FilterModule[];
  @Input() filtersCount: number;
  @Input() filterConfiguration;
  @Input() name: string;
  @Input() value: string;
  @Input() gid: number;

  @Output() filterSkills: EventEmitter<FilteredModule> = new EventEmitter();
  @Output() removeFilter: EventEmitter<RemoveFilterModule> = new EventEmitter();

  emitChange(e) {
    const index: string = /.*([0-9]+)$/.exec(this.name)[1];

    this.filterSkills.emit(
      new FilteredModule(e, Number.parseInt(index), e.target.value)
    );
  }

  emitRemoveFilter(e) {
    const gid = this.gid;
    this.removeFilter.emit(new RemoveFilterModule(e, e.target,this.gid));
  }

  constructor() {}

  ngOnInit() {}

  isSelected(value) {

    const keys = Object.keys(this.filterConfiguration);
    const config = this.filterConfiguration;
    let selected = false;
    const thisgid = this.gid;

    keys.forEach(element => {
      const item = config[element];
        if(item != '000' && item == value && config[thisgid] != value){
          
          selected = true;
          return true;
        }
    });
    return selected;
  }

  isLastFilter() {
    return (
      Object.keys(this.filterConfiguration).length - 1 == this.gid &&
      this.gid != 0
    );
  }

  getFilterGrain() {
    return this.gid == 0 ? "sort by" : "then by";
  }

  consistentClass() {
    return this.filterConfiguration[this.gid] != "000";
  }
}
