import { Component, OnInit, Input } from "@angular/core";

@Component({
  selector: "app-transcorner",
  templateUrl: "./transcorner.component.pug",
  styleUrls: ["./transcorner.component.scss"]
})
export class TranscornerComponent implements OnInit {
  @Input() crnClass: string;
  @Input() wrapperClass: string;
  @Input() hrefid: string = "";
  @Input() customStyl: string;

  constructor() {}

  getCrnClass() {
    return this.getActiveClassFromValidateClasses(
      ".crn-top.crn-bottom.crn-right.crn-left.crn-left-delayed25.crn-left-delayed75.crn-dark.crn-light.crn-green",
      this.crnClass
    );
  }

  getWrapperClass() {
    return this.getActiveClassFromValidateClasses(
      ".crn-concave.crn-on-down.crn-from-top.crn-decay-top",
      this.wrapperClass
    );
  }

  getActiveClassFromValidateClasses(
    serviceClasses: string,
    classes: string = ""
  ) {
    const arr = classes.split(/[\.,\s]/);

    if (arr[0] == "") {
      arr.shift();
    }

    let result = {};
    arr.forEach(function(e) {
      const m = new RegExp(
        "^.*(?:(?:[.]" + e + ")[.][^.].*|(?:[.]" + e + "))$"
      ).test(serviceClasses);
      if (m) {
        result[e] = true;
      }
    });

    return result;
  }

  ngOnInit() {}
}
